.list {
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: center;
  }
  .listItem {
    width: calc((100% - 70px) / 6);
    text-align: center;
    padding: 0 20px;
  }
  .img {
    width: 100%;
    height: 300px;
    display: block;
    margin: 0 auto;
  }
  .title {
    font-size: 18px;
    font-weight: 600;
  }